.UserActions {
  position: absolute;
  right: 42%;
  top: 2%;
  z-index: 10;
}

.UserActions--button {
  display: inline-block;
  cursor: pointer;
  font-family: "Luckiest Guy";
  padding: 10px;
  color: #ff81d2;
  font-size: 25px;
  background-color: white;
  border-radius: 5px;
  border: 5px solid #ffc9ef;
  min-height: 65px;
  min-width: 65px;
}

.UserActions--button:first-of-type {
  margin-right: 5px;
}

@media all and (max-width: 1200px) {
  .UserActions {
    right: 4%;
  }
}


