.Website--Nav {
  font-size: 20px;
}

.Website--NavItem {
  cursor: pointer;
}

.Website--NavItem a {
  color: white;
  text-decoration: none;
}

.Website--NavItem a.active,
.Website--NavItem a.active:hover {
  color: white;
  text-decoration: underline;
}

.Website--NavItem a:hover {
  color: #ff81d2;
  -webkit-transition-delay:0.2s;
       -o-transition-delay:0.2s;
          transition-delay:0.2s;
}


