.Arrow--left,
.Arrow--right {
  cursor: pointer;
}

.Arrow--left {
  margin-left: 5px;
}

.Arrow--right {
  position: absolute;
  top: 34%;
  right: 3.5%;
}

.Arrow--left:before,
.Arrow--right:before {
  content: ' ';
  border: solid #ffc9ef;
  border-width: 0 10px 10px 0;
  display: inline-block;
  padding: 10px;
}

.Arrow--left:before {
  -webkit-transform: rotate(135deg);
      -ms-transform: rotate(135deg);
          transform: rotate(135deg);
}

.Arrow--right:before {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}


