.Controls {
  display: -ms-flexbox;
  display: flex;
  color: #ff81d2;
}

.Control {
  text-align: center;
  border-left: 5px solid #ffc9ef;
  border-bottom: 5px solid #ffc9ef;
  width: 100px;
  height: 100px;
  display: inline-block;
  cursor: pointer;
  border-radius: 0 3px;
}

.Control:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.Control:first-of-type {
  border-left: none;
}

.Control:last-of-type {
  -ms-flex-positive: 100;
      flex-grow: 100;
}

.Control--active {
  border-bottom: 5px solid white;
}

.Control img {
  max-height: 75%;
  max-width: 85%;
  vertical-align: middle;
}

@media all and (max-width: 1200px) {
  .Controls {
    position: absolute;
    background: white;
    top: -81%;
    left: 0;
    border-top: 5px solid #ffc9ef;
  }

  .Control:last-of-type {
    border-right: 5px solid #ffc9ef;
  }
}

@media all and (max-width: 750px) {
  .Controls {
    top: -61%;
  }

  .Control {
    width: 65px;
    height: 65px;
  }
}


